import {
  request
} from './request'


// 
export function getUserList() {
  return request({
    url: "/user/index",
    method: "get",
  }) 
}


// id
export function getRoleIdList() {
  return request({
    url: "/user/create",
    method: "get",
  }) 
}

// 
export function saveSysManager(data) {
  return request({
    url: "/user/save",
    method: "post",
    data: data
  }) 
}

// 
export function deleteUserInfo(id) {
  return request({
    url: "/user/delete",
    method: "get",
    params: {
      id: id
    }
  }) 
}

// 
export function getOneUserInfo(id) {
  return request({
    url: "/user/edit",
    method: "get",
    params: {
      id: id
    }
  }) 
}

// 
export function updateUserInfo(data) {
  return request({
    url: "/user/update",
    method: "post",
    data: data
  }) 
}

