<template>
  <div class="sys-manage">
    <!--  -->
    <div class="search">
      <el-card class="search-wrap">
        <el-form size="small" :inline="true" ref="searchForm" :model="searchForm">
          <el-form-item prop="name">
            <el-input size="mini" v-model="searchForm.name" placeholder="" clearable></el-input>
          </el-form-item>

          <!--  -->
          <el-form-item prop="role_id">
            <el-select v-model="searchForm.role_id" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
            </el-select>
          </el-form-item>
          <!--  -->
          <el-form-item prop="join_date">
            <el-date-picker
              class="select-date"
              v-model="searchForm.join_date"
              type="date"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
          <!--  -->
          <el-form-item class="handle">
            <el-button type="primary" icon="el-icon-search" @click="searchBtn"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button icon="el-icon-delete" @click="clearBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>

    <!--  -->
    <div class="user-table">
      <el-card class="user-table-card" :style="{height: screenHeight}">
        <!--  -->
        <el-row>
          <el-col :span="24">
            <el-button type="primary" icon="el-icon-user" @click="addBtn" size="mini"></el-button>
            <el-button type="primary" icon="el-icon-s-check" @click="addRoleBtn" size="mini"></el-button>
          </el-col>
        </el-row>

        <!--  -->
        <el-row>
          <el-col :span="13">
            <el-col :span="24">
              <p class="list-name">
                <el-tag></el-tag>
              </p>
            </el-col>
            <!--  -->
            <el-table
              :data="userList"
              class="user-table-wrap"
              style="width: 100%"
              :height="scrollHeight"
              v-loading="loading"
              border
            >
              <el-table-column align="center" label="" prop="id" width="100"></el-table-column>
              <el-table-column
                align="center"
                prop="username"
                label=""
                :show-overflow-tooltip="true"
              ></el-table-column>
              <el-table-column width="180" align="center" prop="create_time" label=""></el-table-column>
              <el-table-column width="180" align="center" prop="role.name" label=""></el-table-column>
              <!--  -->
              <el-table-column label="" align="center" width="140px">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="primary"
                    icon="el-icon-edit"
                    @click="editUserBtn(scope.row.id, scope.row.username)"
                    circle
                  ></el-button>
                  <el-popconfirm
                    confirmButtonText=""
                    cancelButtonText=""
                    icon="el-icon-info"
                    iconColor="red"
                    title=""
                    @onConfirm="userDeleteBtn(scope.row.id)"
                  >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      circle
                      slot="reference"
                      style="margin: 0 10px"
                    ></el-button>
                  </el-popconfirm>
                </template>
              </el-table-column>
            </el-table>
          </el-col>

          <!--  -->
          <el-col :span="10" :offset="1">
            <el-col :span="24">
              <p class="list-name">
                <el-tag type="warning"></el-tag>
              </p>
            </el-col>
            <el-table
              :data="roleList"
              class="user-table-wrap"
              style="width: 100%"
              :height="scrollHeight"
              v-loading="roleLoading"
              border
            >
              <el-table-column align="center" label="" prop="id" width="100"></el-table-column>
              <el-table-column align="center" prop="name" label="" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column
                :show-overflow-tooltip="true"
                align="center"
                prop="description"
                label=""
              ></el-table-column>
              <!--  -->
              <el-table-column label="" align="center" width="140px">
                <template slot-scope="scope">
                  <el-tooltip
                    class="item"
                    effect="dark"
                    content=""
                    :enterable="false"
                    placement="top"
                  >
                    <el-button
                      size="mini"
                      type="primary"
                      icon="el-icon-s-custom"
                      @click="showPowerBtn(scope.row.id)"
                      circle
                    ></el-button>
                  </el-tooltip>
                  <el-button
                    size="mini"
                    type="primary"
                    icon="el-icon-edit"
                    @click="editRoleBtn(scope.row.id, scope.row.name)"
                    circle
                  ></el-button>
                  <el-popconfirm
                    confirmButtonText=""
                    cancelButtonText=""
                    icon="el-icon-info"
                    iconColor="red"
                    title=""
                    @onConfirm="roleDeleteBtn(scope.row.id)"
                  >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      circle
                      slot="reference"
                      style="margin: 0 10px"
                    ></el-button>
                  </el-popconfirm>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </el-card>
    </div>

    <!--  -->
    <pagination
      :currentPage="currentPage"
      :perpage="per_page"
      :total="total"
      @handlecurrentchange="handleCurrentChange"
    />

    <!--  -->
    <el-dialog
      :title="editUserTitle"
      :visible.sync="addDialogVisible"
      @close="addClose"
      width="450px"
      center
    >
      <el-form ref="form" :rules="formRules" :model="form" label-width="80px">
        <el-form-item label="" prop="username">
          <el-input size="mini" v-model="form.username" style="width: 140px" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input style="width: 140px" placeholder="" size="mini" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role_id">
          <el-select size="mini" v-model="form.role_id" placeholder="">
            <el-option
              v-for="item in roleIdList"
              :key="item.value"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="addDialogVisible = false"> </el-button>

        <el-button v-if="isAddManager" size="mini" type="primary" @click="addSystemManager"> </el-button>
        <el-button v-else size="mini" type="primary" @click="editSaveManager"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="editRoleTile"
      :visible.sync="addRoleDialogVisible"
      @close="addRoleClose"
      width="450px"
      center
    >
      <el-form ref="roleForm" :rules="roleFormRules" :model="roleForm" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input size="mini" v-model="roleForm.name" style="width: 140px" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            style="width: 140px"
            placeholder=""
            size="mini"
            v-model="roleForm.description"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="addRoleDialogVisible = false"> </el-button>
        <el-button v-if="isAddRole" size="mini" type="primary" @click="saveRoleInfo"> </el-button>
        <el-button v-else size="mini" type="primary" @click="saveEditRoleInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="powerDialogVisible" width="600px" center>
      <div class="power-content">
        <div style="margin: 15px 0;"></div>
        <el-checkbox-group v-model="checkedPowers" @change="handleCheckedPwersChange">
          <el-collapse v-model="activeNames" @change="handleChange">
            <el-collapse-item
              v-for="powerItemObj in powers"
              :key="powerItemObj.controller"
              :title="powerItemObj.controller_text"
              name="1"
            >
              <el-checkbox
                v-for="power in powerItemObj.menu"
                :label="power.id"
                :key="power.id"
                border
                size="mini"
                style="margin: 10px"
              >{{power.name}}</el-checkbox>
            </el-collapse-item>
          </el-collapse>
        </el-checkbox-group>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="powerDialogVisible = false"> </el-button>
        <el-button size="mini" type="primary" @click="updateRolePowerInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "components/common/pagination/Pagination";
import {
  getUserList,
  getRoleIdList,
  saveSysManager,
  getRolesList,
  saveRolueInfo,
  deleteUserInfo,
  getOneUserInfo,
  updateUserInfo,
  getOneRoleInfo,
  updateRoleInfo,
  deleteRoleInfo,
  getAllPower,
  getRolePower,
  updateRolePower,
} from "network/systemroles";
export default {
  name: "SysManage",
  data() {
    return {
      searchForm: {
        name: "",
        state: "",
        join_date: "",
      },
      form: {},
      roleForm: {},
      addRoleDialogVisible: false,

      // 
      currentPage: 1,
      // 
      total: null,
      // 
      per_page: null,
      // 
      roleList: [],
      roleLoading: false,
      // 
      addRoleForm: {
        name: "",
        description: "",
      },
      editRoleForm: {
        id: "",
        name: "",
        description: "",
      },
      isAddRole: null,
      editRoleTile: "",
      // id
      roleIdList: [],

      // 
      userList: [],
      loading: false,
      // 
      addForm: {
        username: "",
        password: "",
        role_id: 5,
      },
      editManagerForm: {
        id: "",
        username: "",
        password: "",
        role_id: "",
      },
      isAddManager: null,
      editUserTitle: "",

      // 
      addDialogVisible: false,

      /**
       * 
       */
      powerDialogVisible: false,

      checkedPowers: [],
      powers: [],
      activeNames: ["1"],
      updatePowerForm: {
        permission_id: [],
        role_id: "",
      },

      /**
       * 
       */
      formRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
        role_id: [{ required: true, message: "", trigger: "blur" }],
      },

      roleFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        description: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  created() {
    this.getAllUserListData();
    this.getAllRoleListData();

    // id
    getRoleIdList().then((res) => {
      let { code, data, msg } = res;
      if (code === 200) {
        this.roleIdList = data;
      } else {
        this.$message.error(msg);
      }
    });

    // 
    getAllPower().then((res) => {
      let { code, data, msg } = res;
      if (code === 200) {
        this.powers = data;
      } else {
        this.$message.error(msg);
      }
    });
  },
  methods: {
    getAllUserListData() {
      this.loading = true;
      getUserList().then((res) => {
        let { code, data, msg } = res;
        if (code === 200) {
          this.userList = data.data;
          // 
          this.currentPage = data.current_page;
          // 
          this.total = data.total;
          // 
          this.per_page = data.per_page;
          this.loading = false;
        } else {
          this.$message.error(msg);
          this.loading = false;
        }
      });
    },
    // 
    getAllRoleListData() {
      this.roleLoading = true;
      getRolesList().then((res) => {
        let { code, data, msg } = res;
        if (code === 200) {
          this.roleList = data;
          this.roleLoading = false;
        } else {
          this.$message.error(msg);
          this.roleLoading = false;
        }
      });
    },

    // 
    showPowerBtn(id) {
      // 
      this.updatePowerForm.permission_id = [];
      this.updatePowerForm.role_id = "";

      this.updatePowerForm.role_id = id;
      getRolePower(id).then((res) => {
        let { code, data, msg } = res;
        if (code === 200) {
          let numbers = data
            ? data.map((item) => {
                return parseInt(item);
              })
            : [];
          this.checkedPowers = numbers;
          this.powerDialogVisible = true;
        } else {
          this.$message.error(msg);
        }
      });
    },

    // 
    updateRolePowerInfo() {
      this.updatePowerForm.permission_id = this.checkedPowers;
      updateRolePower(this.updatePowerForm).then((res) => {
        let { code, msg } = res;
        if (code === 200) {
          this.$message.success(msg)
          this.powerDialogVisible = false
        } else {
          this.$message.error(msg);
        }
      });
    },
    handleChange() {
      return false;
    },

    handleCheckedPwersChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.powers.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.powers.length;
    },

    // 
    clearBtn() {
      this.$refs.searchForm.resetFields();
    },

    // 
    handleCurrentChange(currentpage) {
      console.log(currentpage);
    },

    // 
    searchBtn() {},

    // 
    addBtn() {
      this.editUserTitle = "";
      this.isAddManager = true;
      this.form = this.addForm;
      this.addDialogVisible = true;
    },

    // 
    editUserBtn(id, name) {
      this.isAddManager = false;
      // id
      getOneUserInfo(id).then((res) => {
        let { code, data, msg } = res;
        if (code === 200) {
          this.editManagerForm["username"] = data.username;
          this.editManagerForm["password"] = data.passowrd;
          this.editManagerForm["id"] = data.id;
          this.editManagerForm["role_id"] = data.role_id;
          this.form = this.editManagerForm;

          this.editUserTitle = `${name}`;
          this.addDialogVisible = true;
        } else {
          this.$message.error(msg);
        }
      });
    },

    // 
    editSaveManager() {
      updateUserInfo(this.editManagerForm).then((res) => {
        let { code, msg } = res;
        if (code === 200) {
          this.$message.success(msg);
          this.addDialogVisible = false;
          this.getAllUserListData();
        } else {
          this.$message.error(msg);
        }
      });
    },

    // 
    addRoleBtn() {
      this.editRoleTile = "";
      this.isAddRole = true;
      this.roleForm = this.addRoleForm;
      this.addRoleDialogVisible = true;
    },

    // 
    userDeleteBtn(id) {
      deleteUserInfo(id).then((res) => {
        let { code, msg } = res;
        if (code === 200) {
          this.$message.success(msg);
          this.getAllUserListData();
        } else {
          this.$message.error(msg);
        }
      });
    },

    // 
    addSystemManager() {
      console.log(this.form);
      this.$refs.form.validate((valid) => {
        if (valid) {
          saveSysManager(this.form).then((res) => {
            let { code, msg } = res;
            if (code === 200) {
              this.$message.success(msg);
              this.addDialogVisible = false;
              this.getAllUserListData();
            } else {
              this.$message.error(msg);
            }
          });
        } else {
          return false;
        }
      });
    },

    // 
    saveRoleInfo() {
      this.$refs.roleForm.validate((valid) => {
        if (valid) {
          saveRolueInfo(this.roleForm).then((res) => {
            let { code, msg } = res;
            if (code === 200) {
              this.$message.success(msg);
              this.addRoleDialogVisible = false;
              this.getAllRoleListData();
            } else {
              this.$message.error(msg);
            }
          });
        } else {
          return false;
        }
      });
    },

    // 
    editRoleBtn(id, name) {
      this.isAddRole = false;
      // id
      getOneRoleInfo(id).then((res) => {
        let { code, data, msg } = res;
        if (code === 200) {
          this.editRoleForm["name"] = data.name;
          this.editRoleForm["description"] = data.description;
          this.editRoleForm["id"] = data.id;

          this.roleForm = this.editRoleForm;

          this.editRoleTile = `${name}`;
          this.addRoleDialogVisible = true;
        } else {
          this.$message.error(msg);
        }
      });
    },

    // 
    saveEditRoleInfo() {
      this.$refs.roleForm.validate((valid) => {
        if (valid) {
          updateRoleInfo(this.editRoleForm).then((res) => {
            let { code, msg } = res;
            if (code === 200) {
              this.$message.success(msg);
              this.addRoleDialogVisible = false;
              this.getAllRoleListData();
            } else {
              this.$message.error(msg);
            }
          });
        } else {
          return false;
        }
      });
    },

    // 
    roleDeleteBtn(id) {
      deleteRoleInfo(id).then((res) => {
        let { code, msg } = res;
        if (code === 200) {
          this.$message.success(msg);
          this.getAllRoleListData();
        } else {
          this.$message.error(msg);
        }
      });
    },

    // 
    addClose() {
      this.$refs.form.resetFields();
      this.form = {};
    },

    //
    addRoleClose() {
      this.$refs.roleForm.resetFields();
      this.roleForm = {};
    },
  },
  computed: {
    screenHeight() {
      return this.$store.state.screenHeight - 210 + "px";
    },
    scrollHeight() {
      return this.$store.state.screenHeight - 340 + "px";
    },
  },
  components: {
    Pagination,
  },
};
</script>

